import { mapState } from "vuex";
import FollowMixin from "~/plugins/follow-mixin";
export default {
  name: "AuthorBanner",
  props: { author: String },
  mixins: [FollowMixin],
  data() {
    return { authorInfo: {} };
  },
  computed: {
    ...mapState(["UserInfo"]),
    //
    isSignedIn() {
      return "token" in this.UserInfo;
    },
  },
  created() {
    this.handleGetAuthorInfo();
  },
  methods: {
    // 获取作者信息
    async handleGetAuthorInfo() {
      const { profile } = await this.$axios.$get(`profiles/${this.author}`);
      this.authorInfo = profile;
    },
  },
};
